@import url('https://fonts.googleapis.com/css2?family=Merienda:wght@300..900&display=swap');

* {
    box-sizing: border-box;
}

.alta {
    padding-top: 100px;
    padding-bottom: 100px;
    font-family: "Merienda", cursive;
    background-image: url(https://www.viajarafantasia.com/wp-content/uploads/2023/08/callejon-diagon.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    padding: 20px;
    height: auto;
    color: white;
    text-align: center;
    font-size: 20px;
}

.alta h1 {
    margin-bottom: 60px;
    font-size: 40px;
}

/* ----- Maquetado del formulario de alta ------- */
.alta .alta-form {
    background-color: rgba(0, 0, 0, .7);
    margin-left: 20%;
    margin-right: 20%;
    padding: 20px;
    margin-top: 100px;
    margin-bottom: 100px;
    border-radius: 20px;
    border: 1px solid #a97416;
    transition: all 1s 0s ease-out;
}

.alta .input-group {
    text-align: center;
    padding: 5px;
}

.alta label {
    text-transform: capitalize;
    display: block;
    margin-bottom: 5px;
    padding: 5px;
}

.alta label[for="envio"] {
    display: inline-block;
    margin: 10px 0;
}


.alta input {
    width: 50%;
    height: 30px;
    border-radius: 5px;
    background-color: #95afc0;
    font-family: "Merienda", cursive;
    margin-bottom: 5px;
    border: 1px solid white;
}

.alta input[type="checkbox"] {
    width: 20px;
    height: 20px;
}

.alta .alta-form:hover {
    border: 2px solid #a97416;
    background-color: rgba(0, 0, 0, .9);
}

.alta .error-details {
    color: red;
}

/* ----- Maquetado de la tabla de productos ------- */
.alta th,td {
    /* border: 1px solid black; */
    width: 100%;
    height: 30px;
    padding: 4px;    
}

.alta-tabla  {
    overflow-x: auto;
    background-color: rgba(0, 0, 0, .7);
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    height: auto;
    max-width: 100%;
    padding: 5px;
    border-radius: 20px;
    border: 1px solid #a97416;
    transition: all 1s 0s ease-out;
}

.alta th {
    text-transform: uppercase;
    background-color: #57224e;
    color: white;
    border: 1px solid white;
    border-radius: 5px;
    font-size: 20px;
}

.alta h2 {
    text-align: center;
    font-size: 40px;
}

.alta .centrar {
    text-align: center;    
}

.alta-tabla:hover {
    border: 2px solid #a97416;
    background-color: rgba(0, 0, 0, .9);
}

/* ------------------------------------------ */
.alta table button[id*="btnBorrar-"] {
    margin: 15px auto;
    display: block;
    width: 100px;
    height: 35px;
    background-color: #57224e;
    border-radius: 5px;
    font-size: 20px;
    color: white;
    border: 1px solid white;
    margin-top: 50px;
    font-family: "Merienda", cursive;
    transition: all 1s 0s ease-out;
}

.alta table button[id*="btnBorrar-"]:disabled {
    opacity: 0.5;
}

