@import url('https://fonts.googleapis.com/css2?family=Merienda:wght@300..900&display=swap');

* {
    box-sizing: border-box;
}

.contacto {    
    font-family: "Merienda", cursive;
    background-image: url(https://www.viajarafantasia.com/wp-content/uploads/2023/08/callejon-diagon.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    padding: 20px;
    height: auto;    
    color: white;
    text-align: center;
    font-size: 20px;
    min-height: 600px;
    padding: 20px;
}

.contacto h1 { 
    margin-bottom: 60px;
    font-size: 40px;
}

.contacto .contacto-form {
    background-color: rgba(0, 0, 0, .7);
    margin-left: 20%;
    margin-right: 20%;
    padding: 20px;
    margin-top: 100px;
    margin-bottom: 100px;
    border-radius: 20px;
    border: 1px solid #a97416;
    transition: all 1s 0s ease-out;
}

.contacto .contacto-group {
    text-align: center;
    padding: 3px;
}

.contacto label {
    text-transform: capitalize;
    display: block;
    margin-bottom: 5px;    
}

.contacto form button {    
    margin: 15px auto;
    display: block;
    width: 100px;
    height: 35px;
    background-color: #57224e;
    border-radius: 5px;
    font-size: 20px;
    color: white;
    border: 1px solid white;
    margin-top: 50px;
    font-family: "Merienda", cursive;
    transition: all 1s 0s ease-out;
}

.contacto input {
    width: 50%;
    height: 30px;
    border-radius: 5px;
    background-color:#95afc0;
    font-family: "Merienda", cursive;
    margin-bottom: 5px;
    border: 1px solid white;
}

.contacto textarea {
    width: 50%;
    height: 100px;
    border-radius: 5px;
    background-color:#95afc0;
    font-family: "Merienda", cursive;
    border: 1px solid white;
}

.contacto .error-details {
    color: red;
}

.contacto .contacto-form:hover {
    border: 2px solid #a97416;
    background-color:rgba(0, 0, 0, .9);
}

.contacto form button:hover {
    background-color: white; 
    color: #a97416;    
    border: 1px solid #a97416;
}