@import url('https://fonts.googleapis.com/css2?family=Merienda:wght@300..900&display=swap');

* {
    box-sizing: border-box;
}

.nosotros {
    padding-top: 100px;
    padding-bottom: 100px;
    font-family: "Merienda", cursive;
    background-image: url(https://www.viajarafantasia.com/wp-content/uploads/2023/08/callejon-diagon.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
}

.nosotros-historia {
    padding: 20px;
    height: auto;    
    margin-left: 20%;
    margin-right: 20%;
    text-align: center;
    background-color: rgba(0, 0, 0, .7);
    color: white;
    border: 1px solid #a97416;
    border-radius: 20px;
    font-size: 20px;
    transition: all 1s 0s ease-out;
}

.nosotros-historia h1 {
    margin-bottom: 60px;
    font-size: 40px;
}

.nosotros-historia:hover {
    border: 2px solid #a97416;
    background-color: rgba(0, 0, 0, .9);
}