@import url('https://fonts.googleapis.com/css2?family=Merienda:wght@300..900&display=swap');

* {
    box-sizing: border-box;
}

.inicio {
    padding-top: 100px;
    padding-bottom: 100px;
    font-family: "Merienda", cursive;
    background-image: url(https://www.viajarafantasia.com/wp-content/uploads/2023/08/callejon-diagon.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    padding: 20px;
    height: auto;
    min-height: 800px;
    color: white;
    text-align: center;
    font-size: 20px;
}

.inicio .section-cards-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.inicio section img {
    margin-left: auto;
    margin-right: auto;
    width: 150px;
    height: auto;
    max-width: 100%;
    margin-bottom: 20px;
    transition: all 2s 0s ease-out;
}

.inicio section {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: space-evenly;
    border: 2px solid #a97416;
    width: 350px;
    padding: 15px;
    background-color: rgba(0, 0, 0, .7);
    margin: 25px;
    border-radius: 15px;
    transition: all 1s 0s ease-out;
}

.inicio section h3 {
    font-size: 24px;
    margin-bottom: 20px;
    text-decoration: none;
    color: white;
}

.inicio .section-cards-header h1 {
    color: white;
    margin-bottom: 40px;
}

/* .inicio .section-cards-container p {
    /* text-align: left; 
} */

.inicio section p {
    margin-top: 10px;
}

.inicio section button[id^="btnComprar-"] {
    background-color: #57224e;
    color: white;
    text-decoration: none;
    border: 1px solid white;
    border-radius: 5px;
    font-size: 20px;
    padding: 2px;
    width: 60%;
    min-width: 60%;
    height: 35px;
    line-height: 30px;
    transition: all 1s 0s ease-out;
    margin-left: 20%;
    margin-right: 20%;
    margin-top: 15px;
    font-family: "Merienda", cursive;
}

.inicio section button[id^="btnComprar-"]:disabled {
    opacity: 0.5;
}

.inicio section img:hover {
    width: 300px;
    height: auto;
}

.inicio section button[id^="btnComprar-"]:hover {
    background-color: white;
    color: #a97416;
    border: 1px solid #a97416;
}

.inicio section:hover {
    border: 4px solid #a97416;
    background-color: rgba(0, 0, 0, .9);
}

