@import url('https://fonts.googleapis.com/css2?family=Merienda:wght@300..900&display=swap');

* {
    box-sizing: border-box;
}

.carrito {
    padding-top: 100px;
    padding-bottom: 100px;
    font-family: "Merienda", cursive;
    background-image: url(https://www.viajarafantasia.com/wp-content/uploads/2023/08/callejon-diagon.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
}

.carrito-cuadro {
    overflow-x: auto;
    min-height: 600px;
    padding: 20px;
    height: auto;
    margin-bottom: 100px;
    margin-top: 100px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    background-color: rgba(0, 0, 0, .7);
    color: white;
    border: 1px solid #a97416;
    border-radius: 20px;
    font-size: 20px;
    transition: all 1s 0s ease-out;
    width: 90%;
    max-width: 90%;
}

.carrito-cuadro h1 {
    text-align: center;
    color: white;
    font-size: 40px;    
}

.carrito-cuadro:hover {
    border: 2px solid #a97416;
    background-color: rgba(0, 0, 0, .9);
}

/* ----- Maquetado de la tabla de productos ------- */
.carrito-cuadro th,td {
    width: auto;
    /* height: 30px; */
    padding: 4px;
    text-align: center;
}

.carrito-cuadro table {
    border-collapse: separate; 
    margin-left: auto;
    margin-right: auto;
    width: 100%; 
    height: auto;
    max-width: 100%; 
    padding: 5px;
    border-radius: 20px;    
    transition: all 1s 0s ease-out;
}

.carrito-cuadro th {
    text-transform: uppercase;
    background-color: #57224e;
    color: white;
    border: 1px solid white;
    border-radius: 5px;
    font-size: 20px;
}

.carrito-cuadro h2 {
    /* text-align: center; */
    font-size: 40px;    
}

.carrito-cuadro h4 {
    /* text-align: center; */
    font-size: 25px;
}

/* .carrito-cuadro .centrar {
    text-align: center;
} */


.carrito-cuadro table button[id*="btnBorrar-"] {
    margin: 15px auto;
    display: block;
    font-family: "Merienda", cursive;
    font-size: 20px;
    width: 100px;
    height: 35px;
    border-radius: 5px;    
    border: 1px solid #a97416;
    background-color: white;
    color: #a97416;
}

.carrito-cuadro table button[id*="btnDecrementar-"],
.carrito-cuadro table button[id*="btnIncrementar-"] 
{
    margin: 15px 2px;    
    font-family: "Merienda", cursive;
    width: 40px;
    height: 40px;
    border-radius: 5px;
    font-size: 18px;
    background-color:#95afc0;
    color: white;
}


.carrito__borrar {
    margin: 15px auto;
    display: block;
    font-family: "Merienda", cursive;
    width: 100px;
    height: 40px;
    border-radius: 5px;
    font-size: 20px;
    border: 1px solid white;
    background-color: #a97416;
    color: white;
}

.carrito__pedir {
    margin: 15px auto;
    display: block;
    font-family: "Merienda", cursive;
    width: 100px;
    height: 40px;
    border-radius: 5px;
    font-size: 20px;
    border: 1px solid white;
    background-color: #1b3b26;
    color: white;
}

.carrito-cuadro #wallet_container {
    width: 25%;
    margin: auto;
}
